import AMapLoader from '@amap/amap-jsapi-loader';
import { onMounted, onBeforeUnmount, reactive, shallowReactive } from 'vue';

export default function useInitMap(ele) {
    let position = reactive({
            lng: 0,
            lat: 0,
            info: '',
            address: '',
            id: '',
        }),
        view = shallowReactive({
            selector: null,
            openOptions: false,
            selected: null,
            placeholder: 'search',
            searchData: null,
        }),
        map = null,
        searcher = null,
        marker = null,
        geocoder = null,
        timer = null;

    async function initMap() {
        if (!window.AMap) {
            dblog('--- AMap load script ---');
            window._AMapSecurityConfig = { securityJsCode: '4a9476967d2df985d8da46a99d35f117' };
            await AMapLoader.load({
                key: '3740655a53d086222d88768f76400216', // 申请好的Web端开发者Key，首次调用 load 时必填
                // version: '2.0', // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
                plugins: ['AMap.ToolBar', 'AMap.Scale', 'AMap.PlaceSearch', 'AMap.Geocoder'], // 需要使用的的插件列表
            });
        }
        dblog('--- AMap init ---');
        map = new AMap.Map(ele, { resizeEnable: true, zoom: 11 });
        map.addControl(new AMap.ToolBar());
        map.addControl(new AMap.Scale());
        searcher = new AMap.PlaceSearch({
            city: '全国',
            type: '', //数据类别
            pageSize: 20, //每页结果数,默认10
            pageIndex: 1, //请求页码，默认1
            extensions: 'base', //返回信息详略，默认为base（基本信息）
        });
        marker = new AMap.Marker({
            icon: '//a.amap.com/jsapi_demos/static/demo-center/icons/poi-marker-red.png',
            position: new AMap.LngLat(116.39, 39.9), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
            title: '北京',
        });
        geocoder = new AMap.Geocoder({
            city: '全国',
            radius: 150,
            extensions: 'all',
        });
        bindClick();
        dblog('--- AMap init finish ---');
    }
    function destroyMap() {
        map && map.destroy();
    }
    function searchPosition(keyWord) {
        clearTimeout(timer);
        if (!keyWord) return clearSearchData();
        timer = setTimeout(() => {
            dblog('--- serching poi ---', keyWord);
            map.getCity((city) => {
                searcher.setCity(city.citycode);
                searcher.search(keyWord, (status, result) => {
                    if (status !== 'complete' || !result.poiList.pois.length) return clearSearchData();
                    getSearchData(result.poiList.pois);
                });
            });
        }, 350);
    }
    function bindClick() {
        map.on('click', (position) => {
            let p = new AMap.LngLat(position.lnglat.getLng(), position.lnglat.getLat());
            geocoder.getAddress(p, (status, result) => {
                if (status !== 'complete') return clearSearchData();
                let clicked = {
                    id: '',
                    name: '未命名地址',
                    address: result.regeocode.formattedAddress,
                    location: p,
                };
                dblog('--- click position ---', clicked);
                view.selected = null;
                view.placeholder = 'click';
                view.selector.focus();
                getSearchData([clicked, ...result.regeocode.pois]);
            });
        });
    }
    function setSelector(staus) {
        view.openOptions = staus === 'show';
        if (staus === 'hide') view.placeholder = 'search';
    }
    function clearSearchData() {
        view.searchData = null;
    }
    function getSearchData(data) {
        clearSearchData();
        view.searchData = data;
        dblog('--- get poi data ---', data);
        setSelector('show');
    }
    function setPosition(poiId) {
        setSelector('hide');

        let pos = view.searchData.filter((item) => item.id === poiId);
        if (!pos.length) return;
        position.lng = pos[0].location.lng;
        position.lat = pos[0].location.lat;
        position.info = pos[0].name;
        position.address = pos[0].address;
        position.id = pos[0].id;
        dblog('--- set position ---', position);
        let p = new AMap.LngLat(position.lng, position.lat);
        marker.setPosition(p);
        marker.setTitle(position.info);
        map.clearMap();
        map.add(marker);
        map.setZoomAndCenter(18, p); // map.panTo(p);
    }

    onMounted(() => initMap());
    onBeforeUnmount(() => destroyMap());

    return {
        position,
        view,
        setSelector,
        searchPosition,
        setPosition,
    };
}
