<template>
  <div class="container">
    <IndexHeader>地图导航二维码生成</IndexHeader>
    <QrEditor :qrText="qrText">
      <template #contentTitle>输入内容，开始制作二维码</template>
      <template #tips>功能描述：将地图位置信息转为二维码。</template>
      <template #help>
        <p><span>1. 有效期：</span>本页面地图导航为【静态二维码】，生成后永久有效。</p>
        <p>
          <span>2. 地图导航二维码原理</span
          >：地图导航使用了高德地图API，将您选中的地理位置信息存储在二维码中。扫描二维码后即可打开高德地图查看地点信息，或使用导航功能。
        </p>
      </template>
      <template #content>
        <div class="map">
          <a-select
            v-model:value="selected"
            ref="selector"
            show-search
            :placeholder="
              placeholder === 'click' ? '您点击的是否是这些位置？' : '搜索位置 或 用鼠标点击地图位置'
            "
            :default-active-first-option="false"
            :show-arrow="false"
            :filter-option="false"
            :not-found-content="null"
            :open="openOptions"
            @search="searchPosition"
            @select="setPosition"
            @blur="setSelector('hide')"
            @click="setSelector('show')"
            class="position-input"
          >
            <a-select-option v-for="(item, index) in searchData" :key="item.id" :value="item.id">
              <EnvironmentFilled class="select-icon" v-show="item.id" />
              <QuestionCircleTwoTone class="select-icon" v-show="!item.id" />
              <span> {{ item.name }} - </span>
              <span class="address">{{ item.address }} </span>
            </a-select-option>
          </a-select>
          <div id="amap"></div>
        </div>
      </template>
    </QrEditor>
  </div>
</template>

<script>
import { EnvironmentFilled, QuestionCircleTwoTone } from '@ant-design/icons-vue';
import { computed, toRefs } from 'vue';
import { useHead } from '@vueuse/head';
import useInitMap from '@/hooks/useInitMap';
import IndexHeader from '@/components/IndexHeader.vue';
import QrEditor from '@/components/QrEditor/QrEditor.vue';

export default {
  name: 'Map',
  components: {
    EnvironmentFilled,
    QuestionCircleTwoTone,
    IndexHeader,
    QrEditor
  },
  setup() {
    useHead({
      title: '地图导航二维码生成 - 兔子二维码',
      meta: [
        { name: 'keywords', content: '二维码,地图导航,导航二维码,地理位置' },
        {
          name: 'description',
          content: '将地图位置信息转为二维码'
        }
      ]
    });

    let { position, view, setSelector, searchPosition, setPosition } = useInitMap('amap');

    let qrText = computed(() => {
      if (!position.lng || !position.lat) return '';
      if (position.id) return `https://uri.amap.com/poidetail?poiid=${position.id}&src=tuzim.net`;
      if (position.info) {
        let name = position.info === '未命名地址' ? position.address : position.info;
        return `https://uri.amap.com/marker?position=${position.lng},${position.lat}&name=${name}&src=tuzim.net`;
      }
      return '';
    });

    return {
      qrText,
      ...toRefs(view),
      setSelector,
      searchPosition,
      setPosition
    };
  }
};
</script>

<style lang="less" scoped>
.map {
  position: relative;
  #amap {
    width: 100%;
    height: 440px;
    border-radius: 5px;
  }
  .position-input {
    width: 60%;
    position: absolute;
    top: 3px;
    right: 3px;
    z-index: 99;
    box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.1);
  }
  :deep(.amap-icon img),
  :deep(.amap-marker-content img) {
    width: 25px;
    height: 34px;
  }
}
.select-icon {
  margin-right: 6px;
  font-size: 16px;
  color: #f34234;
}
.address {
  font-size: 12px;
  color: #999;
}
</style>
